import { useEffect, useRef } from 'react';
import { useDatabase } from '../context/DatabaseContext';

interface UseAutoRefreshOptions {
  interval?: number; // en millisecondes
  enabled?: boolean;
  onRefresh?: () => void;
}

export const useAutoRefresh = (options: UseAutoRefreshOptions = {}) => {
  const {
    interval = 30000, // 30 secondes par dfaut
    enabled = true,
    onRefresh
  } = options;

  const { refreshData } = useDatabase();
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const lastRefreshRef = useRef<number>(Date.now());

  useEffect(() => {
    if (!enabled) {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
      return;
    }

    const performRefresh = async () => {
      try {
        // viter les rafrachissements trop frquents
        const now = Date.now();
        if (now - lastRefreshRef.current < 5000) { // Minimum 5 secondes entre les rafrachissements
          return;
        }

        lastRefreshRef.current = now;
        
        // Rafrachir les donnes
        await refreshData();
        
        // Callback personnalis
        if (onRefresh) {
          onRefresh();
        }

        console.log(` Auto-refresh effectu  ${new Date().toLocaleTimeString()}`);
      } catch (error) {
        console.error('Erreur lors du rafrachissement automatique:', error);
      }
    };

    // Premier rafrachissement immdiat
    performRefresh();

    // Configurer l'intervalle
    intervalRef.current = setInterval(performRefresh, interval);

    // Rafrachir quand la page devient visible
    const handleVisibilityChange = () => {
      if (!document.hidden && enabled) {
        performRefresh();
      }
    };

    document.addEventListener('visibilitychange', handleVisibilityChange);

    // Nettoyage
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [interval, enabled, refreshData, onRefresh]);

  // Fonction pour forcer un rafrachissement
  const forceRefresh = async () => {
    try {
      await refreshData();
      if (onRefresh) {
        onRefresh();
      }
      console.log(' Rafrachissement forc effectu');
    } catch (error) {
      console.error('Erreur lors du rafrachissement forc:', error);
    }
  };

  return {
    forceRefresh,
    lastRefresh: lastRefreshRef.current
  };
};
